import React, {useState, useEffect} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';
import { Nav } from 'react-bootstrap';
import {CSSTransition} from 'react-transition-group';
import { connect } from 'react-redux';

function Detail(props){

let [alert, alert변경] = useState(true);
let [누른탭,누른탭변경] = useState(0);
let [스위치, 스위치변경] = useState(false);

useEffect(() => {
  let timer = setTimeout(() => {
    alert변경(false)
  }, 2000);
},[alert]);


let 박스 = styled.div`
    padding:20px;
`;
let { id } = useParams();
let 찾은상품 = props.shoes.find(function(상품){
    return 상품.id == id;
});

let history = useHistory();

    return(
      <div className="container">
        <h2>Detail</h2>
        {
          alert === true
          ?  <div className="my-alert">
               재고가 얼마 남지 않았습니다
             </div>
          : null
        }


        <div className="row">
          <div className="col-md-6">
            <img src= { 찾은상품.img } width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{ 찾은상품.title }</h4>
            <p>{ 찾은상품.content }</p>
            <p>{ 찾은상품.price }</p>
            <Info 재고={props.재고}  />
            <button className="btn btn-danger" onClick={ ()=>{
              let 재고빼기 = [...props.재고];
              재고빼기[0] = 재고빼기[0]-1;
              props.재고변경(재고빼기);

              props.dispatch({type : '항목추가', payload : {id:찾은상품.id,name:찾은상품.title,quan:1}});
              history.push('/cart');
            }}>주문하기</button> 
            <button className="btn btn-danger" onClick={ ()=>{ history.push('/'); } }>뒤로가기</button> 
          </div>
        </div>

        <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
          <Nav.Item>
            <Nav.Link eventKey="link-0" onClick={()=>{스위치변경(false); 누른탭변경(0)}}>상품설명</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="link-1" onClick={()=>{ 스위치변경(false); 누른탭변경(1)}}>배송정보</Nav.Link>
          </Nav.Item>
        </Nav>

        <CSSTransition in={스위치} classNames="wow" timeout={500}>
           <TabContent 누른탭={누른탭} 스위치변경={스위치변경} />
        </CSSTransition>

      </div> 
    )
  }

  function TabContent(props){

    useEffect(()=>{
      props.스위치변경(true)
    })

    if(props.누른탭 === 0 ){
      return <div>00000</div>
    }else if(props.누른탭 === 1){
      return <div>11111</div>
    }

  }


  function Info(props){
    return(
      <p>재고 : {props.재고[0]} </p>
    )
  }

  function 함수명(state){
    return{
        state : state.reducer
    }
   }
   
   export default connect(함수명)(Detail)